.radioCriteriaContainer {
  background-color: white;
  height: 45px;
  width: 130px;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  margin-left: 16px;
}

.radioCriteria {
  display: none;
}

.labelJa , .labelNein {
  display: block;
  height: 90px;
  width: 65px;
  color: #11101d;
  transition: 0.5s;
}

.labelJa {
  background: linear-gradient(
              to bottom, white 0, white 45px, 
              #043d04 45px, #043d04 90px
  );
}

.labelNein {
  background: linear-gradient(
              to bottom, white 0, white 45px, 
              #740707 45px, #740707 90px
  );
}

.radioText {
  display: flex;
  height: 45px;
  width: 65px;
  justify-content: center;
  align-items: center;
  border: 2px solid #11101d;
}

.radioText.Ja {
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
}

.radioText.Nein {
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
}

.radioText:hover {
  cursor: pointer;
}

.radioCriteria:checked + label {
  background-position: 0 -45px;
  color: white;
  transition: 0.5s;
}

.criteria.\32 {
  display: grid;
  align-items: center;
  grid-template-columns: 5fr 1fr;
  padding: 10px;
  font-size: 17px;
  border-bottom: 3px solid #11101d;
}

.childCriteria:nth-child(odd) {
  background-color: rgba(172, 15, 15, 0.212);
}

.criterias {
  display: flex;
  flex-direction: column;
}

.criterias.\32 {
  margin: 80px;
  margin-top: 40px;
}

.criterias.\33 {
  margin-left: 60px;
}

.criteria.\33 {
  font-size: 17px;
  display: grid;
  grid-template-columns: 4fr 0.5fr 0.5fr;
  align-items: center;
  gap: 40px;
  border-bottom: 2px solid #11101d;
  padding: 10px 0 10px 0;
}

.resultCriteria {
  position: relative;
}

.resultCriteriaLabel {
  margin-left: 60px;
  display: grid;
  grid-template-columns: 4fr 0.5fr 0.5fr;
  gap: 40px;
  font-size: 15px;
  margin-top: 20px;
}

.criteria.\33 > p:not(:first-child) {
  text-align: center;
}

.criteriaJaP {
  color: #06a706;
}

.criteriaNeinP {
  color: #d40707;
}

.expandedForm {
  position: relative;
}

.infobtn {
  position: absolute;
  right: 15px;
}

.infobtn.first {
  top: 7px;
}

.infobtn.second {
  top: 76px;
}

.infoContent {
  position: relative;
}

.infobtn i {
  height: 50px;
  min-width: 50px;
  line-height: 50px;
  background-color: rgb(255, 41, 41);
  text-align: center;
  border-radius: 12px;
  transition: .25s ease;
}

.infobtn i:hover {
  background-color: rgb(224, 220, 220);
}

.infoContent::before {
  --scale: 0;
  position: absolute;
  font-size: 12px;
  content: attr(data-tooltip);
  width: max-content;
  color: white;
  background-color: #333;
  transform: translateX(-105%) translateY(-75%) scale(var(--scale));
  border-radius: 12px 12px 0 12px;
  padding: 20px;
  transition: .25s transform;
  transform-origin: right bottom;
}

.infoContent:hover::before {
 --scale: 1;
}