/*XXXXXXXXXXXXXXXXXXX Toggle Checkbox XXXXXXXXXXXXXXXX*/
.toggleContainer {
  margin-left: 70px;
}

.toggle {
  display: none;
}

.toggle + label{
  display: inline-block;
  height: 60px;
  width: 200px;
  position: relative;
  border: 4px solid #11101d;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.toggle + label::before {
  position: absolute;
  border-radius: 50%;
  top: 4px;
  height: 44px;
  width: 44px;
  content: "";
  transition: all 0.3s ease;
  z-index: 3;
}

.toggle + label::after {
  width: 140px;
  text-align: center;
  z-index: 2;
  text-transform: uppercase;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  text-overflow: ellipsis;
  overflow: hidden;
}

.toggle:not(:checked) + label {
  background-color: transparent;
  text-align: right;
}

.toggle:not(:checked) + label::before {
  left: 4px;
  background-color: #11101d;
}

.toggle:not(:checked) + label::after {
  content: attr(data-unchecked);
  right: 0;
  left: auto;
  opacity: 1;
  color: #11101d;
}

.toggle:checked + label {
  text-align: left;
  border-color: #bd0f0f;
}

.toggle:checked + label::after {
  content: attr(data-checked);
  left: 4px;
  right: auto;
  opacity: 1;
  color: #11101d;
}

.toggle:checked + label::before {
  left: 144px;
  background-color: #bd0f0f;
}

