.scoreBar {
  background-color: #11101d;
  border-radius: 20px;
  height: 45px;
  width: 90%;
  margin: 60px;
}

.scoreValue {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 20px;
  opacity: 0;
  height: 100%;
  width: 0;
  transition: 1s ease;
}

.scoreValue.red {
  opacity: 1;
  color: white;
  background: linear-gradient(to right, rgb(46, 3, 3), rgb(240, 87, 87));
  box-shadow: 0 3px 3px -5px rgb(46, 3, 3), 0 2px 5px rgb(240, 87, 87);
}

.scoreValue.yellow {
  opacity: 1;
  color: white;
  background: linear-gradient(to right, rgb(141, 9, 9), rgb(251, 255, 0));
  box-shadow: 0 3px 3px -5px rgb(46, 3, 3), 0 2px 5px rgb(251, 255, 0);
}

.scoreValue.green {
  opacity: 1;
  color: white;
  background: linear-gradient(to right, rgb(43, 73, 8), rgb(21, 255, 0));
  box-shadow: 0 3px 3px -5px rgb(46, 3, 3), 0 2px 5px rgb(243, 240, 41);
}