.settingsCriteria,
.settingsCosts {
  margin: 80px;
  position: relative;
}

.settingsCriteria > p,
.settingsCosts > p {
  font-size: 20px;
  margin: 10px;
}

.criteria.\34 > input:disabled {
  opacity: 0.4;
}


.criteria.\34 {
  font-size: 17px;
  display: grid;
  grid-template-columns: 4fr 0.5fr 0.5fr;
  align-items: center;
  gap: 40px;
  border-bottom: 2px solid #11101d;
  padding: 10px 0 10px 0;
}

.editWeightings {
  position: absolute;
  height: 35px;
  top: 102%;
  right: 260px;
  border: 2px solid #11101d;
  background-color: transparent;
  padding: 5px;
}

.editWeightings:hover {
  background-color: #bd0f0f;
  color: white;
  border-color: #bd0f0f;
}

.sumWeightings {
  position: absolute;
  right: 0;
}

.settingCostTable {
  margin-left: 80px;
  width: 40%;
}

.settingCostTable tr input,
.criteria.\34 > input {
  outline: 0;
  border-width: 0 0 2px;
  border-color: #11101d;
  background-color: transparent;
}

.settingCostTable {
  margin: 0;
}