.qualificationSection {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 40px;
}

.qualificationCriteria {
  font-size: 1rem;
  position: relative;
  width: 80%;
  height: 3rem;
  margin: 1rem;
}

.qualificationInput {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 3px solid rgb(177, 174, 174);
  border-radius: 2rem;
  outline: none;
  color: #11101d;
  padding: 1.25rem;
  background: none;
}

.qualificationInput:hover {
  border-color: rgb(179, 104, 104);
}

.qualificationInput:focus {
  border-color: #740707;
}

.qualificationInput:focus ~ .qualificationLabel, 
.qualificationInput:not(:placeholder-shown).qualificationInput:not(:focus)~.qualificationLabel {
  top: -0.5rem;
  font-size: 0.8rem;
  left: 0.8rem;
}

.qualificationLabel {
  position: absolute;
  left: 1rem;
  top: 0.8rem;
  padding: 0 0.5rem;
  color: #11101d;
  cursor: text;
  transition: top 200ms ease-in,
    left 200ms ease-in,
    font-size 200ms ease-in;
  background-color: rgb(250, 235, 215);
}

.qualificationNotice {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  width: calc(100% - 160px);
  margin: 30px;
  z-index: 2;
}

.qualificationNotice.green {
  background-color: rgba(148, 247, 148, 0.904);
}

.qualificationNotice.red {
  background-color: rgba(233, 137, 137, 0.904);
}

.qualificationNotice > p {
  margin: 20px;
}