.input {
  font-size: 17px;
}

/*XXXXXXXXXXXXXXXXXXX Input Fast XXXXXXXXXXXXXXXX*/
.capacityInputFast {
  margin: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-items: center;
  align-items: center;
  justify-content: space-evenly;
}

.capacityInputCard {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 20px;
  padding: 20px;
  width: 25%;
  height: 120px;
  border: 3px solid #11101d;
}

.capacityInputCard > #cpu {
  opacity: 0.7;
}

@media (max-width: 1186px) {
  .capacityInputCard {
    height: 160px;
  }
}

.capacityInputCard:focus-within {
  border-color: #bd0f0f;
}

.inputCapacityFast {
  outline: 0;
  border-width: 0 0 2px;
  border-color: #11101d;
  background-color: rgb(250, 235, 215);
}

.inputCapacityFast.result {
  opacity: 1;
}

.inputCapacityFast:focus,
.inputCapacityDetailed:focus {
  border-color: #bd0f0f;
}

/*XXXXXXXXXXXXXXXXXXX Input Detail XXXXXXXXXXXXXXXX*/
.capacityInputDetail {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr auto;
  gap: 75px;
  margin: 70px 70px 0 70px;
}

.capacityInputDetailedForm {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: max-content;
  justify-content: start;
  gap: 20px;
}

.capacityInputDetailedCard {
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.inputCapacityDetailed {
  outline: 0;
  border-width: 0 0 2px;
  border-color: #11101d;
  background-color: rgb(250, 235, 215);
}

#growth:disabled {
  opacity: 0.8;
}

.result#growth:disabled {
  opacity: 1;
}

:disabled {
  background-color: rgb(250, 235, 215);
  opacity: 0.3;
}

.disabled {
  opacity: 0.3;
}

#capacityInputDetailedSubmit, #capacityInputDetailedDelete {
  position: absolute;
  top: 190px;
  width: 150px;
  height: 30px;
  background-color: transparent;
  border: 2px solid #11101d;
  transition: all 0.5s ease;
}

#capacityInputDetailedSubmit {
  right: 180px;
}

#capacityInputDetailedDelete {
  right: 0;
}

#capacityInputDetailedSubmit:not(:disabled):hover,
#capacityInputDetailedDelete:not(:disabled):hover {
  background-color: #bd0f0f;
  color: white;
  border-color: #bd0f0f;
  cursor: pointer;
}

/*XXXXXXXXXXXXXXXXXXX Detail Table und Settings Table XXXXXXXXXXXXXXXX*/
#capacityTable > table,
.settingCostTable {
  border-collapse: collapse;
  font-size: 0.9em;
  width: 100%;
  border-radius: 5px 5px 0 0;
  overflow: hidden;
  box-shadow: 0 0 20px rgba(0,0,0, 0.15);
}

#capacityTable table thead tr,
.settingCostTable th {
  background-color: #bd0f0f;
  color: white;
  text-align: left;
  font-weight: bold;
}

#capacityTable table th,
#capacityTable table td,
.settingCostTable th,
.settingCostTable td {
  padding: 12px 15px;
}

#capacityTable table tbody tr,
.settingCostTable tr {
  border-bottom: 1px solid lightgrey;
}

#capacityTable table tbody tr:nth-of-type(even),
.settingCostTable tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

#capacityTable table tbody tr:last-of-type,
.settingCostTable tr:last-of-type {
  border-bottom: 2px solid #bd0f0f;
}

/*XXXXXXXXXXXXXXXXXXX Toggle Checkbox XXXXXXXXXXXXXXXX*/
.toggleContainer {
  margin-left: 70px;
}

.toggle {
  display: none;
}

.toggle + label{
  display: inline-block;
  height: 60px;
  width: 200px;
  position: relative;
  border: 4px solid #11101d;
  border-radius: 30px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.toggle + label::before {
  position: absolute;
  border-radius: 50%;
  top: 4px;
  height: 44px;
  width: 44px;
  content: "";
  transition: all 0.3s ease;
  z-index: 3;
}

.toggle + label::after {
  width: 140px;
  text-align: center;
  z-index: 2;
  text-transform: uppercase;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  text-overflow: ellipsis;
  overflow: hidden;
}

.toggle:not(:checked) + label {
  background-color: transparent;
  text-align: right;
}

.toggle:not(:checked) + label::before {
  left: 4px;
  background-color: #11101d;
}

.toggle:not(:checked) + label::after {
  content: attr(data-unchecked);
  right: 0;
  left: auto;
  opacity: 1;
  color: #11101d;
}

.toggle:checked + label {
  text-align: left;
  border-color: #bd0f0f;
}

.toggle:checked + label::after {
  content: attr(data-checked);
  left: 4px;
  right: auto;
  opacity: 1;
  color: #11101d;
}

.toggle:checked + label::before {
  left: 144px;
  background-color: #bd0f0f;
}

/*XXXXXXXXXXXXXXXXXXX Ergebnis Button XXXXXXXXXXXXXXXX*/
.inputCapacity {
  position: relative;
}

.continue.inputCapacity {
  position: absolute;
  top: 100%;
  right: 0;
  margin-right: 70px;
}



input::-webkit-inner-spin-button,
input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}