@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body {
  position: relative;
  min-height: 100vh;
  width: 100%;
  overflow: hidden;
}

.form {
  position: absolute;
  height: 100%;
  width: calc(100% - 78px);
  left: 78px;
  transition: all 0.5s ease;
  color: #11101d;
  font-size: 15px;
  font-weight: 500;
  margin-top: 0px;
  overflow-x: scroll;
  
  background-color: rgb(247, 243, 243);
  background-color: rgb(250, 235, 215);
}

/*###################### HOME ##############*/

.home {
  font-size: 20px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
}

.home > img {
  width: 17%;
  margin: 50px;
}

.home > p {
  margin-bottom: 50px;
  margin: 40px 200px 40px 200px;
}

/*#################### Tableiste und Weiter Buttons ##############*/
.inputNavbar {
  font-size: 25px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  list-style-type: none;
  border-top: 0px;
  cursor: pointer;
}

.inputNavbar > li {
  display: inline-block;
  width: 50%;
  border: 3px solid #11101d;
  border-top: 0px;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  text-align: center;
}

#inputTabActive {
  background-color: #740707;
  color: white;
  border: 2px solid rgb(250, 235, 215);
  border-top: 0px;
}

#inputTabNotActive {
  background-image: linear-gradient(to top, #f8f3f3 50%, transparent 50%);
  background-size: 100% 200%;
  background-position: 0% 0%;
  transition: 0.4s;
}

#inputTabNotActive:hover {
  border-color: #740707;
  background-position: 0% 100%;
}

.continue{
  appearance: none;
  background: none;
  outline: none;
  text-align: center;
  width: 40%;
  padding: 2.5px 10px;
  border-radius: 8px;
  color: #11101d;
  font-size: 20px;
  border: 3px solid #11101d;
  background-image: linear-gradient(to top, #740707 50%, transparent 50%);
  background-size: 100% 200%;
  background-position: 0% 0%;
  transition: 0.4s;
}

.continue:not(:disabled):hover {
  color: white;
  cursor: pointer;
  background-position: 0% 100%;
  border-color: #740707;
}

#btnToExpandInput {
  margin-top: 15px;
}

#btnToCapacity {
  position: absolute;
  right: 80px;
  top: 105%;
  width: 20%;
  padding: 10px;
}

.expandedForm {
  position: relative;
}

.results {
  display: grid;
  height: 90vh;
  grid-template-columns: minmax(650px, 2fr) minmax(743px, 6fr);
}

@media (max-width: 1479px) {
  .results {
    display: flex;
    flex-direction: column;
  }

  .criterias.\33 {
    margin-right: 80px;
  }

  .resultCriteriaLabel {
    margin-right: 60px;
    gap: 60px !important;
  }
}

.resultTitle {
  font-size: 30px;
  text-align: center;
  margin-top: 20px;
  text-decoration: underline;
  color: #11101d;
}

.resultCapacityCosts {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-left: 0px;
}

.resultCostsSums {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  min-width: 313px;
}

.resultCostsCard {
  position: relative;
  text-align: center;
  justify-content: end;
  font-size: 20px;
  height: 50%;
  width: 60%;
  border: 3px solid #11101d;
  margin-top: 20px;
  padding: 15px;
}

.resultCostAmount {
  position: absolute;
  font-size: 30px;
  font-weight: bold;
  top: 50%;
  left: 50%;
  margin-top: 20px;
  transform: translate(-50%, -50%);
}

.capacityInputFast.result {
  margin-top: 5px;
}
